.img{
    padding:0;
}

.linkText{
    font-size: 14px;
    cursor:pointer;
}

.linkText:hover{
    text-decoration: underline;

}

.bold{
    font-weight: bold;
}

.alignCenter{
    align-items: center;
    justify-content: center;
    font-family: "Dancing Script", cursive;
    font-size: 24px;
    font-weight: 600;
    font-style: normal;
}

.alignEnd{
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items:center;
    padding: 0 1cm;
}

.heading1{
    font-size: 48px;
    font-weight: 600;
}

.heading2{
    font-size: 24px;
}

.heading3{
    font-size: 16px;
}

.heading4{
    font-size: 14px;
}

.homeParent {
    width: 100%;
  }

.homeDivisions{
    display: flex;
    flex-direction: column;
    row-gap: 0.8cm;
}
  
.background-image {
    top:0;
    position: relative;
    width: 100%;
    height: 75vh;
    background-image: url("background-home.jpg");
    background-size: cover;
    background-position: center;
}


  
.homeChild1P{
    height:100%;
    width:100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.homeChild1 {
    color: white;
    display: flex;
    flex-direction: column;
    /* justify-content: center; */
    align-items: center;
    width:70%;
    row-gap: 0.8cm;
    animation: leftSlide 0.5s ease-in;
  }

  @keyframes leftSlide {
    0% {
        transform: translateX(-100%);
        opacity: 0;
      }
      100% {
        transform: translateX(0);
        opacity: 1;
      }
  }
  
  .btn1 {
    font-size: 18px;
    color:white;
    border:5px solid rgb(106, 43, 10);
    background-color: transparent;
    padding:0.4cm 0.8cm;
    border-radius: 25px;
    
  }
  
  .btn1:hover{
    background-color: rgb(106, 43, 10);
  }

  .heading1{
    font-size: 48px;
    font-weight: 600;
  }

  .heading3{
    font-size: 16px;
  }

.footerP{
    color:white;
    background-color: rgb(27, 9, 0);
    width:100%;
    bottom:0;
    height: fit-content;
}

.footerC{
    display: flex;
    flex-direction: column;
    row-gap:0.5cm;
    padding:1cm;
}

.footerC1{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

.col1Footer{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
}

.homeChild2P{
    width:100%;
}

.homeChild2{
    display: flex;
    flex-direction: row;
    justify-content: center;
    column-gap:0.8cm;
    row-gap:0.8cm;
    padding:0.5cm 2cm;
    flex-wrap:wrap;
    animation: downSlide 0.5s ease;
}

@keyframes downSlide {
    0% {
        transform: translateY(-100%);
        opacity: 0;        
      }
      100% {
        
        transform: translateY(0);
        opacity: 1;
      }
  }

.card1{
    color:aliceblue;
    display: flex;
    flex-direction: row;
    align-items: center;
    width:8cm;
    border-radius: 12px;
    background-color: rgb(30, 30, 30);
    box-shadow: 0.5px 0.5px rgba(52, 52, 52, 0.475);
    padding:0.5cm 1.5cm 0.2cm 0.0cm;
}

.card1:hover{
    box-shadow: rgba(0, 0, 0, 0.504) 0px 3px 8px;
    background-color: rgb(41, 41, 41);
    cursor: pointer;
}

.cardCol1{
    width:4cm;
}

.imageProfile{
    width:60%;
    height:auto;
    border-radius: 50%;
}

.cardCol2{
    width:4cm;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    row-gap: 0.3cm;

}

.cardCol2Child{
    display: flex;
    flex-direction: row;
    column-gap: 1cm;
    align-items: center;
    margin:0.3cm 0;
}

.btn2{
    font-size: 14px;
    color:white;
    border:3px solid rgb(134, 53, 9);
    background-color: transparent;
    padding:0.2cm 0.8cm;
    border-radius: 18px;
    
  }
  
  .btn2:hover{
    background-color: rgb(134, 53, 9);
    cursor:pointer;
  }

.homeChild3P{
    width:100%;                         
}

.homeChild3C{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    column-gap:0.8cm;
    padding:0.5cm 2cm;
}

.left-arrow .right-arrow{
    width:2cm;
}

.arrow{
    width:2cm;
    height:auto;
    
}


.card2P:hover{
  
}

.card2P{
    width:8cm;
    border-radius: 12px;
    border:thin solid white;
    box-shadow: -3px 9px 20px 6px rgba(155,152,152,0.67);
    -webkit-box-shadow: -3px 9px 20px 6px rgba(155,152,152,0.67);
    -moz-box-shadow: -3px 9px 20px 6px rgba(155,152,152,0.67);;
}

.card2C{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 0.5cm 1cm;
}

.card2P1{
    width:10cm;
    height:10cm;
    border-radius: 12px;
    border:thin solid white;
    box-shadow: -3px 9px 20px 6px rgba(155,152,152,0.67);
    -webkit-box-shadow: -3px 9px 20px 6px rgba(155,152,152,0.67);
    -moz-box-shadow: -3px 9px 20px 6px rgba(155,152,152,0.67);;
}