.backP{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    background-color: rgb(255, 254, 250);
}

.back{
    width:1cm;
    /* background-color: blue; */
}

.mainTutor{
    width:100rvw;
}

.mainTutorR1{
    height:50vh;
    padding:0.5cm 1cm;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    /* border:thin solid rgb(180, 180, 180); */
    background-image: url("bg.jpg");
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
}

.mainTutorImg{
    width:8cm;
    border-radius: 50%;
}

.mainTutorBox{
    display: flex;
    flex-direction: column;
    row-gap: 0.8cm;
    justify-content: center;
}

.mainTutor1Text{
    display: flex;
    flex-direction: column;
    row-gap: 0.5cm;
    justify-content: center;
    align-items: center;
}

.starP{
    display: flex;
    flex-direction: row;
    column-gap: 0.3cm;
    align-items: center;
}

.star1{
    width:1cm;
    height:auto;
}

.starCount{
    font-size: 20px;
}

.subjectDiv{
    display:flex;
    flex-direction: row;
    align-items: center;
    column-gap: 0.5cm;
    row-gap: 0.5cm;
}

.subjectC{
    padding:0.8cm 0.5cm;
    align-items:center;
    background-color: rgb(0, 162, 106);
    border-radius: 25px;
    width:8cm;
    color:white;
    font-size:20px;
    height:auto;
}

.reviewC{
    padding:0.8cm 0.5cm;
    align-items:center;
    background-color: rgb(187, 0, 106);
    border-radius: 25px;
    width:8cm;
    color:white;
    font-size:20px;
    height:auto;
}